import { Offcanvas } from 'react-bootstrap';

const Sidebar = ({ show, handleClose }) => {
    return (
        <Offcanvas show={show} onHide={handleClose}>
        <Offcanvas.Header closeButton>
            <Offcanvas.Title>Informació</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body>
            Dades sobre la participació de la dona a les festes de Moros i Cristians d'Alcoi extretes a partir de la informació accessible de l'Associació de Sant Jordi https://www.asjordi.org/.
        <br/>
        <br/>
        El apartat "Per filà" fa menció a les dades de la directiva.
        <br/>
        <br/>
        Fe d'errates: la majoria de dades s'han recollit de forma manual, pel que pot haver-hi alguna errata.
            </Offcanvas.Body>
    </Offcanvas>
    )
}

export default Sidebar;